import uniRate from "@/components/uni-rate/uni-rate.vue"

export default {
	data() {
		return {
			listinfo: {},
			logined:false,
			userinfo:{},
			id:"",
			finished:false
		}
	},
	
	onShow() {
		var me = this;
		var userinfo = me.getGlobalUser("userInfos");
		console.log(userinfo);
		if(userinfo != null){
			me.userinfo = userinfo;
			me.logined = userinfo.logined;
			
		}else{
			me.logined = false;
			me.userinfo = {};
		}
	},
	
	onLoad(params) {
		this.rate =params.rate;
		var me = this;
		var id = params.id;
		this.id = id;
		// console.log("id:"+id);
		
		uni.request({
			url:this.mainUrl+'order/order/findOrderInfoById',
			method:"GET",
			data:{
				order_id:id
			},
			success:(res) => {
				console.log("---"+this.rate);
				var listinfo = res.data.data;
				this.listinfo = listinfo;
				// console.log(listinfo);
				if(this.listinfo.status == 3){
					this.finished = true;
				}
			}
		})
	},
	
	methods: {
		confirmFinished:function(){
			uni.request({
				url:this.mainUrl+"user/user/finishOrder",
				method:"GET",
				data:{
					order_id:this.id,
					user_id:this.userinfo.userId
				}
			})
		}
	},
	
	components: {
		uniRate
	}
}
